<!-- 主播短视频----wzh -->
<template>
  <div class="anchor-shot-detail">
    <div class="title">
      <h3>主播短视频</h3>
    </div>
    <detailtop ref="dl" :initData="detailData" :jsonUrl="jsonUrl" v-on:pressDown="keepDo"></detailtop>
    <div class="recommend">
      <h4 v-if="likeShow">猜你喜欢</h4>
      <ul class="recommend-content" ref="contentBox">
        <li v-for="(item,index) in detailData.actorList" :key="index" :id="('box-'+index)" :class="{'active':index===itemNo}">
          <!-- <lazy-image
            :src='item.actorImg'
            :placeholder='placeholder'
          /> -->
          <img :src='item.actorImg' alt="">
          <span>{{item.actorName}}</span>
        </li>
      </ul>
    </div>
    <div class="mask" v-if="maskShow">
      该资产已下线，点击返回体验更多精彩视频
    </div>
    <keyDo ref="keydo" v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
  </div>
</template>

<style scoped="EventDetail">
  .mask{
    width: 1280px;
    height: 720px;
    background-color: rgba(0, 0, 0, .8);
    color: #ffffff;
    position: fixed;
    top: 0;
    left: 0;
    text-align: center;
    z-index: 999;
    line-height: 1050px;
    font-size: 40px;
  }
  .anchor-shot-detail {
    width: 1280px;
    height: 720px;
    background: url("../../../assets/detail/detail-bg.png");
    overflow: hidden;
  }

  .anchor-shot-detail .title {
    position: relative;
    left: -20px;
    display: block;
    width: 254px;
    height: 58px;
    background: rgb(5, 44, 85);
    line-height: 58px;
    font-size: 25px;
    font-weight: 300;
    text-align: center;
    transform: skewX(-30deg)
  }

  .title h3 {
    transform: skewX(30deg)
  }

  .recommend {
    margin-left: 55px;
    width: 10000000px;
  }

  .recommend h4 {
    font-size: 20px;
    opacity: 0.4;
    line-height: 24px;
    margin-top: 20px;
  }

.recommend-content li img{
  width: 100%;
  height: 100%;
  position: relative;
}
  .recommend-content li {
    float: left;
    width: 100px;
    height: 100px;
    margin-right: 83px;
    margin-top: 20px;
    background: url("../../../assets/detail/anchor-recommend.png") no-repeat;
    background-size:100% 100%;
    background-size: cover;
    text-align: center;
    position: relative;
  }

  .recommend-content li.active {
  background: url("../../../assets/detail/anchor-recommend-active.png") no-repeat;
  background-size:100% 100%;
    background-size: cover;
  }

  .recommend-content span {
    position: absolute;
    bottom: 0px;
    left: 34px;
    font-size: 16px;
  }
</style>

<script>
import c from '../../../js/common.js';
import detailtop from '../detail-top.vue';
import { HTTPUtil as api } from '../../../fetch/api.js';
import { mapGetters } from 'vuex';
import { client as yh} from '../../../js/client.js';
import {bi} from '../../../js/logApi.js'
export default {
  data () {
    return {
      detailData: [],
      isShow: false,
      itemNo: -1,
      lastFocus: 0,
      jsonUrl: '',
      placeholder: require('../../../assets/img_loading_352x190.png'),
      maskShow: false,
      likeShow: true,
      actorRecParameter: '&actorRec=1&actor=1',
      startTime: 0
    }
  },
  mounted () {
    this.startTime = new Date() - 0;
  },
  created () {
    if (this.$route.params.jsonUrl) {
      this.jsonUrl = this.$route.params.jsonUrl
      let url = this.$route.params.jsonUrl + this.actorRecParameter + this.behindParams;
      localStorage.setItem('detailJsonUrl', this.jsonUrl);
      this.getDetailData(url);
    } else if (this.$route.query.jsonUrl) {
      c.startTime();
      bi.start('0201')
      this.jsonUrl = `${this.$route.query.jsonUrl}&p=${this.$route.query.p}&k=${this.$route.query.k}&v=${this.$route.query.v}&assetId=${this.$route.query.assetId}&c=${this.$route.query.c}`;
      let url = this.jsonUrl + this.actorRecParameter + this.behindParams;
      localStorage.setItem('detailJsonUrl', this.jsonUrl);
      this.getDetailData(url);
    } else {
      let url = localStorage.getItem('detailJsonUrl') + this.actorRecParameter + this.behindParams;
      this.getDetailData(url);
    }
    // yh.initPlayer(60, 90, 480, 270);
    // this.playVideo();
  },
  computed: {
    ...mapGetters(['behindParams', 'navpos'])
  },
  components: {
    detailtop
  },
  methods: {
    getDetailData (url) {
      // let type = this.$route.params.bp.type;
      // let route =  this.$router;
      if (!url) {
        this.maskShow = true;
        setTimeout(function(){
          // c.routerBack(route, undefined)
           window.location.href = './../esport/jx/index'
        },1500)
      }
      console.log('获取详情页信息' + url);
      // api.jsonp(url, (res) => {
      // if(res.data == null){
      //     this.maskShow = true;
      //   let down = JSON.parse(localStorage.getItem('historyListsr'));
      //   let deleteUrl =  this.$route.params.jsonUrl;
      //   let i = down.forEach(( item,index )=>{
      //     if( deleteUrl.indexOf(item.jsonUrl) != -1  ) {
      //          yh.delHistory(index)
      //     }
      //   })
      //     return;
      //   }else{
      //     this.maskShow = false;
      //   }
      //   console.log(res);
      //   this.detailData = res.data
      //   if(this.detailData.actorList.length == 0){
      //     this.likeShow = false;
      //   }else{
      //     this.likeShow = true;
      //   }
      // })
      api.jsonp(url, (res) => {
        if (res.data == null){
          this.maskShow = true;
          let down = JSON.parse(localStorage.getItem('historyListsr'));
          if (down != null) {
            let deleteUrl = this.$route.params.jsonUrl;
            down.forEach((item, index) => {
              if (item.jsonUrl.indexOf(deleteUrl) != -1) {
                yh.delHistory(index)
              }
            });
          }
          // let id = new URLSearchParams( this.$route.params.jsonUrl.split('?')[1]).get('assetId');
          let id = this.GetRequest(this.$route.params.jsonUrl.split('?')[1]).assetId;
          let urlPath = 'http://112.18.251.100:7077/uds/cloud/collection/del?version=1'
          let data = {
            siteId: yh.siteId,
            userId: yh.userId,
            collectType: 1, // 收藏类型(0-主播,1-资产,2-专题)
            relateId: id
          }
          api.post(urlPath, data, (res) => {
            console.log(res);
            if (res.mes === 'ok') { console.log('删除收藏成功'); }
          })
          return
        } else {
          this.maskShow = false;
        }
        this.detailData = res.data;
        this.newJump();
        if (this.detailData.assetList.length == 0){
          this.likeShow = false;
        } else {
          this.likeShow = true;
        }
        // yh.initPlayer(60,90,480,270);
        // this.playVideo();
      });
    },
    playVideo () {
      let url = this.jsonUrl
      yh.showVideo(url)
    },

    keyCode (kc) {
      if (kc === 'down') {

      } else if (kc === 'up') {
        this.up();
      } else if (kc === 'left') {
        this.left();
      } else if (kc === 'right') {
        this.right();
      } else if (kc === 'KeyEnter') {
        this.enter();
      } else if (kc === 'KeyBack') {
        this.$store.commit('GET_ISSHOWPLAY', false);
        if (this.$route.params.bp && this.$route.params.bp.type){
          c.routerBack(this.$router, this.$route.params.bp.type)
        }else {
          c.routerBack(this.$router, undefined)
        }
      }
    },
    GetRequest (url) {
      var theRequest = new Object();
      let strs = url.split('&');
      for (var i = 0; i < strs.length; i++) {
        theRequest[strs[i].split('=')[0]] = unescape(strs[i].split('=')[1]);
      }
      return theRequest;
    },
    up () {
      this.isShow = false
      this.lastFocus = this.itemNo
      this.itemNo = -1
      this.$refs.dl.getKeyListen();
    },
    left () {
      if (this.itemNo === 0) return;
      if (this.itemNo >= 6) {
        this.$refs.contentBox.style.marginLeft = -(this.itemNo - 6) * 183 + 'px'
      }
      this.itemNo--
    },
    right () {
      if (this.itemNo === this.detailData.actorList.length - 1) return
      this.itemNo++
      if (this.itemNo >= 7) {
        this.$refs.contentBox.style.marginLeft = -(this.itemNo - 6) * 183 + 'px'
      }
    },
    enter () {
      // yh.closePlayer();
      localStorage.setItem('BI_recmd_id', 3);
      let url = this.detailData.actorList[this.itemNo].jsonUrl;
      let bp = this.detailData.actorList[this.itemNo];
      this.$store.commit('GET_ISSHOWPLAY', false);
      this.setLogJump();
      this.$router.push({name: 'anchorDetail', params: {jsonUrl: url, bp: bp}});
      let recBiObj = {
        pos_id: '',
        recmd_id: '3',
        page_type: '0301',
        page_id: this.detailData.assetId,
        cid: this.detailData.assetId,
        click_type: '3'
      }
      bi.jumpRecommend(recBiObj);
    },
    newJump (){
      if (c.isJump() == 'jump'){
        c.removeJump();
        let jumpObj = {
          page_type: '0301',
          page_id: this.detailData.assetId,
          parent_page_type: c.getParentPageType(),
          parent_page_id: c.getParentPageId()
        }
        bi.newJump(jumpObj)
      }
    },
    keepDo (isShow, name, position) {
      // if(this.detailData.actorList.length == 0){
      //   this.$refs.dl.getKeyListen();
      //   return;
      // }
      // if (position === 'down') {
      //   this.isShow = true
      //   this.itemNo = this.lastFocus
      // }
      if (this.detailData.actorList){
        if (this.detailData.actorList.length == 0){
          this.$refs.dl.getKeyListen();
          this.isShow = false;
          return;
        }
      }
      if (position === 'down') {
        this.isShow = true
        this.itemNo = this.lastFocus
      }
    },
    setLogJump (){
      c.setJump();
      c.setParentPageType('0301');
      c.setParentPageId(this.detailData.assetId);
    }
  }
}
</script>



